import Link from "next/link";
import Image from "next/image";
import Header from "../components/Header";

export default function NewsPage() {
  return (
    <main className="min-h-screen bg-gradient-to-b from-primary-dark to-slate-900 text-text">
     

      {/* Featured Article Section */}
      <section className="py-12 px-4">
        <div className="max-w-6xl mx-auto">
          <h2 className="text-3xl md:text-4xl font-bold mb-8 text-center">
            Featured Article
          </h2>
          <div className="rounded-lg overflow-hidden shadow-lg">
            {/* Featured Article Card */}
            <Link href="/news/featured-article" passHref>
              <a className="block">
                <Image
                  src="/featured-news.jpg" // Replace with your actual image path
                  alt="Featured News"
                  width={1200}
                  height={600}
                  className="w-full object-cover"
                />
                <div className="p-6 bg-white/5">
                  <h3 className="text-2xl font-semibold text-text mb-4">
                    The Future of Artificial Intelligence in Healthcare
                  </h3>
                  <p className="text-lg text-gray-400 mb-6">
                    Explore how AI is transforming healthcare systems worldwide.
                  </p>
                  <button className="inline-block py-2 px-4 bg-secondary hover:bg-secondary-light text-text font-medium rounded-md transition duration-300">
                    Read More
                  </button>
                </div>
              </a>
            </Link>
          </div>
        </div>
      </section>

      {/* Recent Articles Section */}
      <section className="py-12 px-4">
        <div className="max-w-6xl mx-auto">
          <h2 className="text-3xl md:text-4xl font-bold mb-8 text-center">
            Recent Articles
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {[
              {
                title: "Quantum Computing Breakthrough",
                description:
                  "Scientists achieve major milestone in quantum computing.",
                image: "/quantum-computing.jpg", // Replace with your actual image path
                slug: "quantum-computing-breakthrough",
              },
              {
                title: "Sustainable Energy Innovations",
                description:
                  "New technologies are paving the way for cleaner energy.",
                image: "/sustainable-energy.jpg", // Replace with your actual image path
                slug: "sustainable-energy-innovations",
              },
              {
                title: "Advancements in Space Exploration",
                description:
                  "NASA reveals plans for future missions to Mars and beyond.",
                image: "/space-exploration.jpg", // Replace with your actual image path
                slug: "advancements-in-space-exploration",
              },
            ].map((article) => (
              <Link key={article.slug} href={`/news/${article.slug}`} passHref>
                <a className="block bg-white/5 border border-gray-700 rounded-lg overflow-hidden shadow-md hover:shadow-lg transition-all">
                  {/* Article Image */}
                  <Image
                    src={article.image}
                    alt={article.title}
                    width={500}
                    height={300}
                    className="w-full h-48 object-cover"
                  />
                  {/* Article Details */}
                  <div className="p-6">
                    <h3 className="text-xl font-semibold text-text mb-2">
                      {article.title}
                    </h3>
                    <p className="text-gray-400">{article.description}</p>
                  </div>
                </a>
              </Link>
            ))}
          </div>
        </div>
      </section>
    </main>
  );
}